import React, { useState } from "react";
import { parse } from 'parse-gedcom'; 

function App() {
  const [file, setFile] = useState(null);
  const [results, setResults] = useState(null);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };
  console.log("App v0.1503");
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!file) {
      alert("Please upload a GEDCOM file.");
      return;
    }

    const reader = new FileReader();

    reader.onload = async (event) => {
      const gedcomData = event.target.result;

      try {
        // Ensure gedcomData is a valid string
        if (typeof gedcomData !== 'string') {
          throw new Error("Invalid GEDCOM data. Data should be a string.");
        }
        
        // Parse the GEDCOM data using parse-gedcom
        const parsedData = parse(gedcomData);  // No need to split the string


        // Extract marriage events from parsed data
        const couplesWithMarriageEvents = extractMarriageEvents(parsedData);

        // Send couples to the backend for comparison
        const response = await fetch(
          "https://us-central1-gendep82-matching.cloudfunctions.net/compareCouples",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ couples: couplesWithMarriageEvents }),
          }
        );

        const data = await response.json();
        setResults(data);
      } catch (error) {
        console.error("Error parsing GEDCOM file or matching with Google Sheets:", error);
      }
    };

    reader.readAsText(file);  // Ensure the file is read as text
  };

  const extractMarriageEvents = (parsedData) => {
    const couples = [];

    // Access the 'children' array from the parsedData root object
    const entities = parsedData.children;

    if (!Array.isArray(entities)) {
      console.error("Expected an array of GEDCOM entities, but got something else.");
      return couples;
    }

    // Loop through the 'children' to find family records (FAM)
    entities.forEach(entry => {
      if (entry.type === 'FAM') {
    
        console.log("Family Entry:", entry);  // Log the entire family entry
    
        const husband = entry.children.find(item => item.type === 'HUSB');
        const wife = entry.children.find(item => item.type === 'WIFE');
        const marriage = entry.children.find(item => item.type === 'MARR');
        const contract = entry.children.find(item => item.type === 'MARC');
        const searchStatus = contract.children.find(item => item.type === '_FNA');
       
        if (husband && wife && marriage) {
          // Use the 'pointer' field to find the individual details for the husband and wife
          const husbandId = husband.data.pointer;
          const wifeId = wife.data.pointer;
          const place = marriage.children.find(item => item.tag === 'PLAC');
    
          console.log("Husband ID (pointer):", husbandId);  // Log the extracted pointer
          console.log("Wife ID (pointer):", wifeId);        // Log the extracted pointer
          console.log("Marriage Place:", place.value);        // Log the extracted pointer
          console.log("Contract?:", contract);        // Log the extracted pointer
          console.log("Search contract:", searchStatus);        // Log the extracted pointer
    
          if (husbandId && wifeId && place) {
            // Find the corresponding individuals using the 'pointer' field
            const husbandDetails = entities.find(item => item.tag === 'INDI' && item.pointer === husbandId);
            const wifeDetails = entities.find(item => item.tag === 'INDI' && item.pointer === wifeId);
    
            if (husbandDetails && wifeDetails) {
              const couple = {
                husbandFirstName: getGivenName(husbandDetails),
                husbandLastName: getSurname(husbandDetails),
                wifeFirstName: getGivenName(wifeDetails),
                wifeLastName: getSurname(wifeDetails),
                marriagePlace: place.value,
              };
    
              console.log(
                `Husband: ${couple.husbandFirstName} ${couple.husbandLastName}, ` +
                `Wife: ${couple.wifeFirstName} ${couple.wifeLastName}, ` +
                `Marriage Place: ${couple.marriagePlace}`
              );
    
              couples.push(couple);
            }
          }
        }
      }
    });

    return couples;
  };

  const getGivenName = (person) => {
    const name = person.tree.find(item => item.tag === 'NAME');
    return name ? name.data.split('/')[0] : '';
  };

  const getSurname = (person) => {
    const name = person.tree.find(item => item.tag === 'NAME');
    return name ? name.data.split('/')[1] : '';
  };

  return (
    <div>
      <h1>Upload GEDCOM File</h1>
      <form onSubmit={handleSubmit}>
        <input type="file" accept=".ged" onChange={handleFileChange} />
        <button type="submit">Upload and Compare</button>
      </form>

      {results && (
        <div>
          <h2>Comparison Results:</h2>
          <pre>{JSON.stringify(results, null, 2)}</pre>
        </div>
      )}
    </div>
  );
}

export default App;